@import url('https://fonts.googleapis.com/css2?family=Josefin+Slab&family=Poppins&family=Roboto:wght@400;500&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}
body{
  background-color: black;
}

.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  width: 100%; 
  height: 700px; 
  text-align: center;
  margin: 28px auto;
  border-radius: 10px;
  padding: 32px;
  background-image: url(/public/bg-image-ferns.avif);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 28px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
  
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid grey;
  outline: 2px solid grey;
  outline: none;
  width: 75rem;
  background: transparent;
  color: #fff;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 16px;
  border:  none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  background: #24a0ed;
  color: white;
}



.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
  
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px 0; /* Added margin here */
  color: white;
  background: #3d3d3d;
  outline: 2px solid gray;
  opacity: 85%;
  padding: 16px;
  border-radius: 5px;
}



.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: black;
}

.edit-icon {
  color: black;
}